import { Icon28HomeOutline, Icon28GridSquareOutline, Icon28AttachOutline, Icon28AllCategoriesOutline, Icon28LinkCircleOutline } from '@vkontakte/icons';
import { Epic, Tabbar, TabbarItem, SplitLayout } from '@vkontakte/vkui';

import Home from '../Epics/Home';
import Headers from '../Epics/Headers';
import Content from '../Epics/Content';
import Params from '../Epics/Params';
import Other from '../Epics/Other';
import Connection from '../Epics/Connection';

const MiniInterface = ({ s, changeTheme, send, isApp, sendError, modal, separatorGroup }) => {
    const { story, url, path, headers, popout } = s.g();
    const apiurl = require('url');

    const toStory = e => {
        switch (story) {
            case "headers":
                if (e === 'headers') return;

                let h = [...headers];
                let errorIndex;
                for (let i = 0; i < headers.length; i++) {
                    const e = h[i][0];
                    h[i].splice(2, 1);

                    if (e === '') {
                        h[i][2] = true;
                        s.s('headers', h);
                        return sendError("Удалите или переименуйте заголовки без названия, чтобы продолжить.");
                    }

                    if (-1 !== (errorIndex =
                        h.findIndex((array, index) => {
                            if (index === i) return false;
                            return array[0] === e;
                        }))
                    ) {
                        h[i][2] = true;
                        h[errorIndex][2] = true;
                        s.s('headers', h);
                        return sendError(<div>Заголовок <b>"{e}"</b> повторяется несколько раз. Удалите или переименуйте повторяющиеся заголовки, чтобы продолжить.</div>);
                    }
                    s.s('headers', h);
                }
                break;

            case "params":
                if (e === 'params') return;
                else if (!url && path.length === 0) {
                    s.s('url', '');
                    break;
                }

                const string = path.filter(e => e[0] !== '').map(e => e.map(e => encodeURIComponent(e)).join('=')).join('&');
                const parsedUrl = apiurl.parse(url.trim() === '' ? '/' : url.match(/^(https?:\/\/)/i) || url.startsWith('/') ? url : 'http://' + url);
                s.s('url', `${url.match(/^(https?:\/\/)/i) ? parsedUrl.protocol + '//' : ''}${parsedUrl.hostname ? parsedUrl.hostname + (parsedUrl.port ? ':' + parsedUrl.port : '') : ""}${parsedUrl.pathname || ''}${string === '' ? '' : '?' + string}${parsedUrl.hash || ''}`);
                break;

            default:
                break;
        }
        s.s('story', e);
    }

    return (
        <SplitLayout
            popout={popout}
            modal={modal}
        >
            <Epic
                activeStory={story}
                tabbar={story !== 'conn' &&
                    <Tabbar>
                        <TabbarItem
                            onClick={() => toStory('home')}
                            selected={story === 'home'}
                            text="Главная"
                        ><Icon28HomeOutline /></TabbarItem>
                        <TabbarItem
                            onClick={() => toStory('headers')}
                            selected={story === 'headers'}
                            text="Заголовки"
                        ><Icon28GridSquareOutline /></TabbarItem>
                        <TabbarItem
                            onClick={() => toStory('params')}
                            selected={story === 'params'}
                            text="URL"
                        ><Icon28LinkCircleOutline /></TabbarItem>
                        <TabbarItem
                            onClick={() => toStory('content')}
                            selected={story === 'content'}
                            text="Контент"
                        ><Icon28AttachOutline /></TabbarItem>
                        <TabbarItem
                            onClick={() => toStory('other')}
                            selected={story === 'other'}
                            text="Еще"
                        ><Icon28AllCategoriesOutline /></TabbarItem>
                    </Tabbar>
                }
                popout={popout}
            >
                <Home
                    id="home"
                    s={s}
                    send={send}
                    separatorGroup={separatorGroup}
                />

                <Headers
                    id="headers"
                    s={s}
                />

                <Content
                    id="content"
                    s={s}
                />

                <Params
                    id="params"
                    s={s}
                />

                <Other
                    id="other"
                    isApp={isApp}
                    changeTheme={changeTheme}
                    s={s}
                />

                <Connection
                    id="conn"
                />
            </Epic>
        </SplitLayout>
    );
}

export default MiniInterface;