import { usePlatform } from '@vkontakte/vkui';
import { useRef, useEffect, useState } from 'react';

const Textarea = ({ className, txtStyle = {}, style = {}, title, onBlur = () => { }, ...props }) => {
    const [border, setBorder] = useState(false);
    const platform = usePlatform();
    const input = useRef(null);

    const update = () => {
        let e = input.current || { style: {}, scrollHeight: '0' };

        e.style.height = 'auto';
        e.style.height = (e.scrollHeight) + "px";
    }

    useEffect(() => {
        document.fonts.ready.then(update);
        window.addEventListener('resize', update);

        // eslint-disable-next-line
    }, []);

    useEffect(update);

    return (
        <div className={className} style={{ position: "relative", ...style }}>
            <textarea className={title ? 'withTitle' : ''} style={{ height: "auto", ...txtStyle }} ref={input} rows={1} onFocus={() => setBorder(true)} onBlur={(e) => { setBorder(false); onBlur(e); }} {...props} />
            <div className={`border border--${platform}`} style={border ? { border: '1px solid var(--accent)' } : {}} />
            <div className={`txt-bg border--${platform}`} />
            {title &&
                <div className={`txtTitle border--${platform}`}>
                    <div>{title}</div>
                </div>
            }
        </div>
    );
}

export default Textarea;