import { usePlatform } from '@vkontakte/vkui';

const Value = ({ className, txtStyle = {}, style = {}, children, ...props }) => {
    const platform = usePlatform();

    return (
        <div className={className} style={{ position: "relative", ...style }}>
            <div className="input" style={txtStyle} {...props}>{children || <div className="unselect" style={{opacity: 0.7}}>Пусто.</div>}</div>
            <div className={`border border--${platform}`} />
            <div className={`txt-bg border--${platform}`} />
        </div>
    );
}

export default Value;