import { useState } from "react";
import { ActionSheet, Group, Panel, PanelHeader, SimpleCell, Switch, View, ActionSheetItem } from "@vkontakte/vkui";
import { Icon28PaletteOutline, Icon16Chevron, Icon28HomeOutline, Icon28LinkOutline, Icon28BugOutline, Icon28FavoriteOutline } from '@vkontakte/icons';
import bridge from '@vkontakte/vk-bridge';

const Other = ({ id, s, isApp, changeTheme }) => {
    const [palette, setPalette] = useState();
    const { redirect, homeIcon, theme, fav } = s.g();

    return (
        <View id={id} activePanel="other">
            <Panel id="other">
                <PanelHeader>Ещё</PanelHeader>

                <Group>
                    <SimpleCell
                        getRootRef={setPalette}
                        style={{ padding: "0 12px" }}
                        before={<Icon28PaletteOutline />}
                        after={<Icon16Chevron style={{ color: "var(--icon_secondary)" }} />}
                        onClick={() => s.s('popout', 
                            <ActionSheet
                                onClose={() => s.s('popout', null)}
                                iosCloseItem={<ActionSheetItem autoclose mode="cancel">Отменить</ActionSheetItem>}
                                toggleRef={palette}
                            >
                                <ActionSheetItem selectable checked={theme === 'light'} autoclose onClick={() => changeTheme('1')}>
                                    Светлая
                                </ActionSheetItem>
                                <ActionSheetItem selectable checked={theme === 'dark'} autoclose onClick={() => changeTheme('2')}>
                                    Тёмная
                                </ActionSheetItem>
                                {isApp &&
                                    <ActionSheetItem selectable checked={theme === 'app'} autoclose onClick={() => changeTheme('')}>
                                        Как в ВК
                                </ActionSheetItem>}
                            </ActionSheet>
                        )}
                    >Внешний вид</SimpleCell>
                    <SimpleCell
                        disabled
                        style={{ padding: "0 12px" }}
                        before={<Icon28LinkOutline />}
                        after={
                            <Switch
                                style={{ cursor: "pointer" }}
                                checked={redirect}
                                onChange={() => s.s('redirect', e => !e)}
                            />}
                    >Переадресации</SimpleCell>
                    {homeIcon && <SimpleCell
                        style={{ padding: "0 12px" }}
                        before={<Icon28HomeOutline />}
                        onClick={() => bridge.send("VKWebAppAddToHomeScreen")}
                    >Добавить на главный экран</SimpleCell>}
                    {!fav && isApp && <SimpleCell
                        style={{ padding: "0 12px" }}
                        before={<Icon28FavoriteOutline />}
                        onClick={() => bridge.send("VKWebAppAddToFavorites")}
                    >Добавить сервис в избранные</SimpleCell>}
                    <SimpleCell
                        style={{ padding: "0 12px" }}
                        before={<Icon28BugOutline />}
                        className="a"
                        target="_blank"
                        href="https://vk.me/club192302489"
                    >Сообщить о баге/ошибке</SimpleCell>
                </Group>
                <div style={{ opacity: 0.6 }} align="center">Версия 1.2 Mini</div>
            </Panel>
        </View>
    );
}

export default Other;