import { useState, useEffect, useRef } from 'react';
import { Button, Alert } from "@vkontakte/vkui";
import { Icon24AddSquareOutline, Icon56SettingsOutline, Icon24Cancel, Icon20DeleteOutline } from '@vkontakte/icons';
import Textarea from '../Elements/Textarea';
import Input from '../Elements/Input';

const Params = ({ s }) => {
    const [path, setPath] = useState([]);
    const [updatePath, toUpdatePath] = useState();
    const first = useState(1);
    const ref = useRef();

    const { url, onBlurUrl } = s.g();
    const apiurl = require('url');

    useEffect(() => {
        if (url === ref.current) return;

        ref.current = url;
        setPath([...(new URLSearchParams(apiurl.parse(url).query || '').entries())]);
    }, [onBlurUrl]);

    useEffect(() => {
        if (first[0]) return first[1](0);

        const string = new URLSearchParams(path.filter(e => e[0] + e[1] !== '')).toString();
        const newUrl = apiurl.format({ ...(apiurl.parse(url)), search: string ? '?' + string : '' });
        s.s('url', newUrl);
        ref.current = newUrl;
    }, [updatePath]);

    return (
        <>
            <div className="Div" style={{ display: "flex" }}>
                <Button
                    before={<Icon24AddSquareOutline width={20} height={20} />}
                    size="m"
                    stretched
                    style={{ marginRight: "8px" }}
                    onClick={() => {
                        let newPath = [...path];
                        newPath.unshift(['', '']);
                        setPath(newPath);
                    }}
                >Добавить</Button>
                <Button
                    size="m"
                    stretched
                    mode="destructive"
                    disabled={path.length === 0}
                    before={<Icon20DeleteOutline />}
                    onClick={() => s.s('popout',
                        <Alert
                            header="Удалить ВСЕ заголовки?"
                            text="Подтвердите действие."
                            onClose={() => s.s('popout', null)}
                            actions={[
                                {
                                    title: "Отмена",
                                    autoclose: true,
                                    mode: 'cancel'
                                },
                                {
                                    title: "Удалить",
                                    mode: "destructive",
                                    action: () => {
                                        setPath([]);
                                        toUpdatePath(Math.random);
                                        s.s('popout', null);
                                    }
                                }
                            ]}
                        />
                    )}
                >Очистить всё</Button>
            </div>
            {path.length === 0 ?
                <div className="placeholder" align="center">
                    <Icon56SettingsOutline />
                    <div>Список URL-параметров пуст</div>
                    <code>/?v=1.2&#38;message=hello+world</code>
                </div>
                :
                <>
                    {path.map((i, k) => (
                        <div className="Div" style={k === path.length - 1 ? { marginBottom: 0 } : {}} key={k}>
                            {k !== 0 && <div className="sep" style={{ margin: "12px 0" }} />}
                            <div style={{ display: "flex", marginBottom: "5px", alignItems: "stretch" }}>
                                <Input
                                    onBlur={() => toUpdatePath(Math.random)}
                                    style={{ flex: 1 }}
                                    txtStyle={{ padding: '7px 9px', fontSize: '11pt' }}
                                    placeholder="Параметр"
                                    value={i[0]}
                                    onChange={({ target: { value: e } }) => setPath(i => {
                                        let path = [...i];
                                        path[k].splice(2, 1);
                                        path[k][0] = e;
                                        return path;
                                    })}
                                />
                                <Button
                                    size="m"
                                    mode="destructive"
                                    onClick={() => s.s('popout',
                                        <Alert
                                            header="Удалить параметр?"
                                            text="Подтвердите действие."
                                            onClose={() => s.s('popout', null)}
                                            actions={
                                                [
                                                    {
                                                        title: "Отмена",
                                                        autoclose: true,
                                                        mode: 'cancel'
                                                    },
                                                    {
                                                        title: "Удалить",
                                                        mode: "destructive",
                                                        action: () => {
                                                            let newPath = [...path];
                                                            newPath.splice(k, 1);
                                                            setPath(newPath);
                                                            toUpdatePath(Math.random);
                                                            s.s('popout', null);
                                                        }
                                                    }
                                                ]
                                            }
                                        />
                                    )}
                                    style={{ marginLeft: "5px", padding: '0 8px' }}>
                                    <Icon24Cancel />
                                </Button>
                            </div>
                            <Textarea
                                onBlur={() => toUpdatePath(Math.random)}
                                placeholder="Значение"
                                txtStyle={{ maxHeight: '87px', padding: '7px 9px', fontSize: '11pt' }}
                                value={i[1]}
                                onChange={({ target: { value: e } }) => setPath(i => {
                                    let path = [...i];
                                    path[k].splice(2, 1);
                                    path[k][1] = e;
                                    return path;
                                })}
                            />
                        </div>
                    ))}
                </>
            }
        </>
    );
}

export default Params;