import { Button, Alert, NativeSelect } from '@vkontakte/vkui';
import { Icon20DeleteOutline } from '@vkontakte/icons';
import Textarea from '../Elements/Textarea';

const types = [
    ['application/json', 'JSON [application/json]'],
    ['application/x-www-form-urlencoded', 'URL Параметры [application/x-www-form-urlencoded]'],
    ['text/plain', 'Текст [text/plain]'],
    [undefined, 'Другое [из заголовков]']
];

const Body = ({ s }) => {
    const { body, headers } = s.g();

    return (
        <>
            <div className="Div">
                <Button
                    size="m"
                    style={{ width: "100%" }}
                    mode="destructive"
                    disabled={!body}
                    before={<Icon20DeleteOutline />}
                    onClick={() => s.s('popout',
                        <Alert
                            header="Очистить весь контент?"
                            text="Подтвердите действие."
                            onClose={() => s.s('popout', null)}
                            actions={
                                [
                                    {
                                        title: "Отмена",
                                        autoclose: true,
                                        mode: 'cancel'
                                    },
                                    {
                                        title: "Очистить",
                                        mode: "destructive",
                                        action: () => {
                                            s.s('body', '');
                                            s.s('popout', null);
                                        }
                                    }
                                ]
                            }
                        />
                    )}
                >Стереть всё</Button>
            </div>
            <div className="Div">
                <div className="title">тип даннных</div>
                <NativeSelect
                    value={types.findIndex(e => {
                        if (e[0] === undefined) return true;

                        return e[0] === (headers.find(e => e[0] === 'content-type') || [])[1];
                    })}
                    onChange={({ target: { value: e } }) => s.s('headers', h => {
                        if (!types[e]) return h;

                        let headers = [...h];
                        const i = headers.findIndex(e => e[0] === 'content-type');

                        if (Number(e) === 3) {
                            if (i === -1) return h;
                            headers.splice(i, 1);
                        } else if (i === -1) headers.push(['content-type', types[e][0]]);
                        else headers[i] = ['content-type', types[e][0]];

                        return headers;
                    })}
                >
                    {types.map((e, key) => (
                        <option value={key} key={key}>{e[1]}</option>
                    ))}
                </NativeSelect>
            </div>
            <div className="Div" style={{ marginBottom: 0 }}>
                <Textarea rows={10} placeholder='Пусто.' value={body} onChange={({ target: { value: e } }) => s.s('body', e)} />
            </div>
        </>
    );
}

export default Body;