import { useState, useRef, useEffect } from 'react';
import { Touch } from '@vkontakte/vkui';

const Swipe = ({ style, children, s, id, ...props }) => {
    const [swipe, setSwipe] = useState(true);
    const ref = useRef();
    const { [`x${id}`]: x = 0, needUpdate } = s.g();

    const calc = () => {
        if (!ref.current) return 0;

        setSwipe(ref.current.scrollWidth !== ref.current.offsetWidth);
        return -(ref.current.scrollWidth - ref.current.offsetWidth);
    }

    useEffect(() => {
        calc();

        if (needUpdate) s.s('needUpdate', false);
        else ref.current.style.transform = `translateX(${x}px)`;
        
        window.addEventListener('resize', () => {
            if (!ref.current) return;

            calc();
            ref.current.style.transition = `transform 0.2s ease 0s`;
            ref.current.style.transform = `translateX(0px)`;
            s.s(`x${id}`, 0);
        });
        // eslint-disable-next-line
    }, []);

    return (
        <Touch
            style={{ overflowX: "hidden", ...style }}
            onMoveX={e => {
                if (!swipe) return;

                let res = x + e.shiftX;
                res = isNaN(res) ? x : res;
                ref.current.style.transition = ``;
                ref.current.style.transform = `translateX(${res}px)`;
            }}

            onEnd={e => {
                if (!swipe) return;
                let res = x + e.shiftX;
                const max = calc();

                if (res > 0) {
                    res = 0;
                } else if (res < max) {
                    res = max;
                }

                res = isNaN(res) ? x : res;
                ref.current.style.transition = `transform 0.2s ease 0s`;
                ref.current.style.transform = `translateX(${res}px)`;
                s.s(`x${id}`, res);
            }}
        >
            <div
                ref={ref}
                {...props}
            >
                {children}
            </div>
        </Touch>
    );
}

export default Swipe;