import { useState } from "react";
import { View, Panel, PanelHeader, Button, Group } from "@vkontakte/vkui";
import Textarea from '../Elements/Textarea';
import Swipe from '../Elements/Swipe';
import Response from '../Elements/Response';

import { Icon24SendOutline } from '@vkontakte/icons';

const Home = ({ id, send, s, separatorGroup }) => {
    const [, setClicks] = useState(0);
    const { loading, url, method } = s.g();

    return (
        <View id={id} activePanel='home'>
            <Panel id="home">
                <PanelHeader onClick={() => {
                    setClicks(e => {
                        e++;
                        if (e === 3) import('../Elements/eruda').then(({ default: eruda }) => { });
                        return e;
                    });
                }}>Requests</PanelHeader>

                <Group separator={separatorGroup ? "auto" : "hide"}>
                    <Textarea
                        autoCapitalize="none"
                        spellCheck="false"
                        autoCorrect="off"
                        className="Div"
                        placeholder='Ссылка'
                        value={url}
                        onKeyPress={e => {
                            if (e.code === 'Enter') !loading && send();
                        }}
                        txtStyle={{ maxHeight: '133px' }}
                        onChange={e => s.s('url', e.target.value.replace(/\n/g, ''))}
                    />
                    <Swipe className="methodList" s={s} id="methods">
                        <Button stopPropagation={false} size='m' mode={method === 0 ? 'primary' : 'outline'} onClick={() => s.s('method', 0)}>GET</Button>
                        <Button stopPropagation={false} size='m' mode={method === 1 ? 'primary' : 'outline'} onClick={() => s.s('method', 1)}>POST</Button>
                        <Button stopPropagation={false} size='m' mode={method === 2 ? 'primary' : 'outline'} onClick={() => s.s('method', 2)}>PUT</Button>
                        <Button stopPropagation={false} size='m' mode={method === 3 ? 'primary' : 'outline'} onClick={() => s.s('method', 3)}>PATCH</Button>
                        <Button stopPropagation={false} size='m' mode={method === 4 ? 'primary' : 'outline'} onClick={() => s.s('method', 4)}>DELETE</Button>
                        <Button stopPropagation={false} size='m' mode={method === 5 ? 'primary' : 'outline'} onClick={() => s.s('method', 5)}>HEAD</Button>
                        <Button stopPropagation={false} size='m' mode={method === 6 ? 'primary' : 'outline'} onClick={() => s.s('method', 6)} style={{ marginRight: 0 }}>OPTIONS</Button>
                    </Swipe>
                    <div className="Div">
                        <Button
                            size="m"
                            style={{ width: '100%' }}
                            disabled={!!loading}
                            className="send"
                            onClick={send}
                            before={!loading && <Icon24SendOutline width={22} height={22} />}
                        >
                            <div style={{ fontWeight: 800, textTransform: "uppercase", verticalAlign: "-3px", display: "inline" }}>
                                {loading || "Отправить"}
                            </div>
                        </Button>
                    </div>
                </Group>

                {!separatorGroup && <div className="sep" style={{ margin: "0 12px" }} />}

                <Group>
                    <Response s={s} />
                </Group>
            </Panel>
        </View>
    );
}

export default Home;