import { useEffect, useState } from 'react';
import { withAdaptivity, ViewWidth, AdaptivityProvider, AppRoot, ConfigProvider, ScreenSpinner, Alert, ModalRoot, ModalCard, Button, Avatar, ModalPage, ModalPageHeader, SimpleCell, ActionSheet, ActionSheetItem, Switch } from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
import { useHistory, useLocation } from "react-router-dom";

import '@vkontakte/vkui/dist/vkui.css';
import './style.css';
import { Icon56SettingsOutline, Icon28BugOutline, Icon28PaletteOutline, Icon16Chevron, Icon28LinkOutline, Icon28HomeOutline, Icon28FavoriteOutline } from '@vkontakte/icons';

import MiniInterface from './Interfaces/MiniInterface';
import Desktop from './Interfaces/Desktop';

const useValue = (def = {}) => {
    const [v, setValue] = useState(def);

    const s = (name, value) => setValue(prev => ({
        ...prev,
        [name]: typeof value === 'function' ? value(prev[name]) : value
    }));

    const g = (name) => name ? v[name] : v;

    return { s, g };
}

const App = ({ viewWidth }) => {
    const history = useHistory();
    const location = useLocation();

    const s = useValue({
        url: "",
        method: 0,
        body: '',
        redirect: true,
        headers: [],
        popout: null,
        tabs: "res",
        activeModal: null,
        story: 'home',
        fav: false,
        homeIcon: false,
        scheme: 'bright_light',
        theme: "light",
        isDesktop: window.innerWidth >= 999,
        webTabs: "url",
        close: true
    });
    const { popout, scheme, url, headers, body, redirect, method, theme, appTheme, activeModal, isDesktop, palette, fav, homeIcon, close } = s.g();

    const themes = ['app', 'light', 'dark'];
    const isApp = !bridge.isStandalone() && !isDesktop;
    const separatorGroup = viewWidth >= ViewWidth.SMALL_TABLET;

    const closeOnboarding = () => {
        s.s('activeModal', null);
        bridge.send('VKWebAppStorageSet', { key: "onboarding", value: 'ok' });
    }

    const checkConnection = () => s.s('story', e => navigator.onLine ? e === 'conn' ? 'home' : e : "conn");

    const modal = (
        <ModalRoot activeModal={(location.state && location.state.modal) ? activeModal : null} onClose={closeOnboarding}>
            <ModalCard
                id="onboarding1"
                header="Добро пожаловать!"
                icon={<Avatar mode="app" src='/requests/icons/icon150.png' size={72} />}
                onClose={closeOnboarding}
                actions={<Button size="m" onClick={() => s.s('activeModal', 'onboarding2')}>Дальше</Button>}
            >
                <div style={{ marginTop: '5px', opacity: 0.7, textAlign: "center" }}>Отправляйте HTTP, REST и SOAP запросы легко и не открывая консоль.</div>
            </ModalCard>

            <ModalCard
                id="onboarding2"
                icon={<Icon56SettingsOutline />}
                onClose={closeOnboarding}
                actions={<Button size="m" onClick={() => {
                    bridge.send("VKWebAppAddToFavorites");
                    closeOnboarding();
                }}>Дальше</Button>}
                header="Меняйте заголовки, контент и параметры запроса"
            >
                <div style={{ marginTop: '5px', opacity: 0.7, textAlign: "center" }}>Всё нужное на панели {isDesktop ? 'слева' : "снизу"}.</div>
            </ModalCard>

            <ModalPage
                id="settings"
                onClose={() => s.s('activeModal', null)}
                header={<ModalPageHeader>Настройки</ModalPageHeader>}
            >
                <SimpleCell
                    getRootRef={(e) => s.s('palette', e)}
                    style={{ padding: "0 16px" }}
                    before={<Icon28PaletteOutline />}
                    after={<Icon16Chevron style={{ color: "var(--icon_secondary)" }} />}
                    onClick={() => s.s('popout',
                        <ActionSheet
                            onClose={() => s.s('popout', null)}
                            iosCloseItem={<ActionSheetItem autoclose mode="cancel">Отменить</ActionSheetItem>}
                            toggleRef={palette}
                        >
                            <ActionSheetItem selectable checked={theme === 'light'} autoclose onClick={() => changeTheme('1')}>
                                Светлая
                            </ActionSheetItem>
                            <ActionSheetItem selectable checked={theme === 'dark'} autoclose onClick={() => changeTheme('2')}>
                                Тёмная
                                </ActionSheetItem>
                            {isApp &&
                                <ActionSheetItem selectable checked={theme === 'app'} autoclose onClick={() => changeTheme('')}>
                                    Как в ВК
                                </ActionSheetItem>}
                        </ActionSheet>
                    )}
                >Внешний вид</SimpleCell>
                <SimpleCell
                    disabled
                    style={{ padding: "0 16px" }}
                    before={<Icon28LinkOutline />}
                    after={
                        <Switch
                            style={{ cursor: "pointer" }}
                            checked={redirect}
                            onChange={() => s.s('redirect', e => !e)}
                        />}
                >Переадресации</SimpleCell>
                {homeIcon && <SimpleCell
                    style={{ padding: "0 16px" }}
                    before={<Icon28HomeOutline />}
                    onClick={() => bridge.send("VKWebAppAddToHomeScreen")}
                >Добавить на главный экран</SimpleCell>}
                {!fav && isApp && <SimpleCell
                    style={{ padding: "0 16px" }}
                    before={<Icon28FavoriteOutline />}
                    onClick={() => bridge.send("VKWebAppAddToFavorites")}
                >Добавить сервис в избранные</SimpleCell>}
                <SimpleCell
                    style={{ padding: "0 16px" }}
                    before={<Icon28BugOutline />}
                    className="a"
                    target="_blank"
                    href="https://vk.me/club192302489"
                >Сообщить о баге/ошибке</SimpleCell>
                <div style={{ opacity: 0.6, padding: '4px 0 12px 0' }} align="center">Версия 1.2 {isDesktop ? 'Desktop' : "Mini"}</div>
            </ModalPage>
        </ModalRoot >
    );

    useEffect(() => {
        history.replace('');
        s.s('close');

        bridge.subscribe(({ detail: { type, data } }) => {
            if (data === undefined) return;
            console.log(type, data);

            switch (type) {
                case 'VKWebAppUpdateConfig':
                    s.s('appTheme', data.scheme);
                    break;

                case 'VKWebAppAddToFavoritesResult':
                    s.s('fav', data.result);
                    break;

                case 'VKWebAppAddToHomeScreenResult':
                    s.s('homeIcon', false);
                    break;

                default:
                    break;
            }

        });

        window.addEventListener('resize', () => {
            s.s('isDesktop', window.innerWidth >= 999);
            s.s('needUpdate', true);
        });

        bridge.send("VKWebAppAddToHomeScreenInfo")
            .then(e => s.s('homeIcon', e.is_feature_supported && !e.is_added_to_home_screen));

        bridge.send("VKWebAppStorageGet", { "keys": ["onboarding"] })
            .then(e => e.keys[0].value !== 'ok' && s.s('activeModal', 'onboarding1'));

        if (isApp) {
            if (window.location.search.slice(1).split('&').find(e => e === "vk_is_favorite=1")) s.s('fav', true);

            bridge.send("VKWebAppStorageGet", { "keys": ["theme"] })
                .then(e => s.s('theme', themes[e.keys[0].value] || 'app'));
        } else s.s('theme', themes[localStorage.theme] || 'light');

        console.log(`- vk mini app?\n- ${isApp ? 'yes' : 'no'}!`);

        checkConnection();
        setInterval(checkConnection, 1000);
        bridge.send('VKWebAppInit');
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        let scheme;

        switch (theme) {
            case 'dark':
                scheme = "space_gray";
                break;

            case 'light':
                scheme = "bright_light";
                break;

            default:
                scheme = appTheme || "bright_light";
                break;
        }

        console.log('theme:', scheme);
        s.s('scheme', scheme);
        bridge.send("VKWebAppSetViewSettings",
            scheme === 'bright_light' ?
                {
                    status_bar_style: "dark",
                    action_bar_color: "#ffffff",
                    navigation_bar_color: '#ffffff'
                } : {
                    status_bar_style: "light",
                    action_bar_color: "#19191a",
                    navigation_bar_color: "#000000"
                }
        );

        const html = document.querySelector('html');
        html.style.backgroundColor =
            separatorGroup ?
                scheme === 'bright_light' ? '#ebedf0' : '#0a0a0a'
                :
                scheme === 'bright_light' ? '#ffffff' : '#19191a';
        // eslint-disable-next-line 
    }, [appTheme, theme, separatorGroup]);

    useEffect(() => {
        if (popout && !popout.props.size && !(location.state && location.state.popout)) history.push('', { popout: 1, modal: location.state && location.state.modal });
        else if (!popout && (location.state && location.state.popout)) history.goBack();
        // eslint-disable-next-line
    }, [popout]);

    useEffect(() => {
        if (activeModal && !(location.state && location.state.modal)) history.push('', { modal: 1, popout: location.state && location.state.popout });
        else if (!activeModal && (location.state && location.state.modal)) history.goBack();
        // eslint-disable-next-line
    }, [activeModal]);

    useEffect(() => {
        if (close) return;

        if (!popout && (location.state && location.state.popout)) history.goBack();
        else if (popout && !(location.state && location.state.popout)) popout.props.onClose();

        if (!activeModal && (location.state && location.state.modal)) history.goBack();
        else if (activeModal && !(location.state && location.state.modal)) modal.props.children.find(e => e.props.id === activeModal).props.onClose();
    }, [location]);

    const sendError = e => s.s('popout',
        <Alert
            header="Произошла ошибка"
            text={<div style={{ wordWrap: 'break-word' }}>{e}</div>}
            onClose={() => s.s('popout', null)}
            actions={[{
                title: "Закрыть",
                autoclose: true,
                mode: "cancel"
            }]}
        />
    );

    const checkHeaders = () => {
        let h = [...headers];
        let errorIndex;
        for (let i = 0; i < headers.length; i++) {
            const e = h[i][0];
            h[i].splice(2, 1);

            if (e === '') {
                h[i][2] = true;
                return "Удалите или переименуйте заголовки без названия, чтобы продолжить.";
            }

            if (-1 !== (errorIndex =
                h.findIndex((array, index) => {
                    if (index === i) return false;
                    return array[0] === e;
                }))
            ) {
                h[i][2] = true;
                h[errorIndex][2] = true;
                return <div>Заголовок <b>"{e}"</b> повторяется несколько раз. Удалите или переименуйте повторяющиеся заголовки, чтобы продолжить.</div>;
            }
        }
        return 1;
    }

    const send = async () => {
        if (url.trim() === '') return sendError('Введите ссылку.');
        else if (method === 5 && body) {
            s.s('webTabs', 'body');
            return sendError('Нельзя прикрепить контент к HEAD-запросу.');
        }
        s.s('loading', 'Загрузка...');
        s.s('popout', <ScreenSpinner />);

        const errorHeaders = checkHeaders();
        if (errorHeaders !== 1) {
            s.s('loading');
            s.s('webTabs', 'headers');
            return sendError(errorHeaders);
        }

        s.s('res');
        s.s('tabs');
        let json;
        try {
            const res = await fetch('https://req.knnrv.ru/v2/send', {
                method: "POST",
                body: JSON.stringify({
                    url: url,
                    method: method,
                    redirect: redirect,
                    headers: {
                        "user-agent": window.navigator.userAgent,
                        ...(headers.reduce((e, k) => {
                            e[k[0]] = k[1];
                            return e;
                        }, {}))
                    },
                    body: body
                }),
                headers: {
                    "content-type": "application/json",
                    'vk-auth': window.location.search
                }
            });
            json = await res.json();
        } catch (e) {
            s.s('loading');
            return sendError(`Ошибка при отправке запроса на сервер. Попробуйте позже 😳`);
        }

        if (json.error) {
            bridge.send("VKWebAppTapticNotificationOccurred", { "type": "warning" });
            if (json.redirects && json.redirects.length > 1) {
                s.s('res', json);
                s.s('popout', null);
            }
            else sendError(<div dangerouslySetInnerHTML={{ __html: json.error }} />);
        }
        else {
            s.s('res', json);
            if (json.jsonHtml) s.s('tabs', 'json');
            else s.s('tabs', 'content');
            bridge.send("VKWebAppTapticNotificationOccurred", { "type": "success" });
            s.s('popout', null);
        }

        s.s('loading');
    }

    const changeTheme = (e) => {
        s.s('theme', themes[e] || 'app');
        if (isApp) bridge.send('VKWebAppStorageSet', { key: "theme", value: e });
        else localStorage.theme = e;
    }

    return (
        <ConfigProvider scheme={scheme} platform={isDesktop ? "ios" : undefined}>
            <AppRoot>
                {isDesktop ?
                    <Desktop s={s} send={send} modal={modal} />
                    :
                    <MiniInterface s={s} send={send} changeTheme={changeTheme} isApp={isApp} sendError={sendError} separatorGroup={separatorGroup} modal={modal} />
                }
            </AppRoot>
        </ConfigProvider>
    );
}

const AdaptivityApp = withAdaptivity(App, { viewWidth: true });

// eslint-disable-next-line
export default () => (
    <AdaptivityProvider>
        <AdaptivityApp />
    </AdaptivityProvider>
);
