import { Group, Panel, TabsItem, Button, NativeSelect, Tabs, PanelHeader } from "@vkontakte/vkui";
import { Icon24SendOutline, Icon20GearOutline } from '@vkontakte/icons';

import Textarea from '../Elements/Textarea';
import Swipe from '../Elements/Swipe';
import Headers from './Headers';
import Params from './Params';
import Body from './Body';

const LeftPanel = ({ s, send }) => {
    const { url, loading, method, webTabs } = s.g();

    return (
        <Panel>
            <PanelHeader />
            <Group>
                <Textarea
                    autoCapitalize="none"
                    spellCheck="false"
                    autoCorrect="off"
                    className="Div"
                    placeholder='Ссылка'
                    value={url}
                    onKeyPress={e => {
                        if (e.code === 'Enter') !loading && send();
                    }}
                    txtStyle={{ maxHeight: '133px' }}
                    onChange={e => s.s('url', e.target.value.replace(/\n/g, ''))}
                    onBlur={() => s.s('onBlurUrl', Math.random)}
                />
                <div className="Div" style={{ display: "flex" }}>
                    <Button
                        size="m"
                        style={{ flex: 1, marginRight: '8px' }}
                        disabled={!!loading}
                        className="send"
                        onClick={send}
                        stretched
                        before={!loading && <Icon24SendOutline width={22} height={22} />}
                    >
                        <div style={{ fontWeight: 800, textTransform: "uppercase", verticalAlign: "-3px", display: "inline" }}>
                            {loading || "Отправить"}
                        </div>
                    </Button>
                    <NativeSelect value={method} onChange={({ target: { value: e } }) => s.s('method', Number(e))}>
                        <option value={0}>GET</option>
                        <option value={1}>POST</option>
                        <option value={2}>PUT</option>
                        <option value={3}>PATCH</option>
                        <option value={4}>DELETE</option>
                        <option value={5}>HEAD</option>
                        <option value={6}>OPTIONS</option>
                    </NativeSelect>
                </div>
            </Group>
            <Group>
                <Swipe style={{ marginTop: "12px", marginBottom: "12px" }} s={s} id="webTabs">
                    <Tabs mode="buttons" style={{ padding: 0 }}>
                        <TabsItem
                            onClick={() => s.s('activeModal', 'settings')}
                            style={{ background: 'var(--button_primary_background)', color: "var(--button_primary_foreground)", padding: '0 8px' }}
                        ><Icon20GearOutline /></TabsItem>
                        <TabsItem
                            selected={webTabs === 'url'}
                            onClick={() => s.s('webTabs', 'url')}
                            style={{ border: '1px solid var(--panel_tab_active_background)' }}
                        >URL</TabsItem>
                        <TabsItem
                            selected={webTabs === 'headers'}
                            onClick={() => s.s('webTabs', 'headers')}
                            style={{ border: '1px solid var(--panel_tab_active_background)' }}
                        >Заголовки</TabsItem>
                        <TabsItem
                            selected={webTabs === 'body'}
                            onClick={() => s.s('webTabs', 'body')}
                            style={{ border: '1px solid var(--panel_tab_active_background)' }}
                        >Контент</TabsItem>
                    </Tabs>
                    {webTabs === 'headers' ?
                        <Headers s={s} />
                        : webTabs === 'url' ?
                            <Params s={s} />
                            : webTabs === 'body' ?
                                <Body s={s} />
                                : null}
                </Swipe>
            </Group>
        </Panel>
    );
}

export default LeftPanel;