import { PanelHeader, SplitCol, SplitLayout } from '@vkontakte/vkui';

import LeftPanel from '../Desktop/LeftPanel';
import RightPanel from '../Desktop/RightPanel';

const Desktop = ({ s, send, modal }) => {
    const { popout } = s.g();

    return (
        <SplitLayout
            modal={modal}
            popout={popout}
            style={{ justifyContent: "center", maxWidth: '1400px', marginLeft: 'auto', marginRight: 'auto' }}
            header={
                <PanelHeader
                    style={{ zIndex: 15 }}
                    separator={false}
                >Requests</PanelHeader>}
        >
            <SplitCol style={{ flex: 1, margin: '0 6px 0px 12px', overflowY: "auto" }}>
                <LeftPanel s={s} send={send} />
            </SplitCol>
            <SplitCol style={{ flex: 1, margin: '0 12px 0 6px', overflowY: "auto" }}>
                <RightPanel s={s} />
            </SplitCol>
        </SplitLayout>
    );
}

export default Desktop;