import { Tabs, TabsItem, SimpleCell, Switch } from '@vkontakte/vkui';
import { Icon20ArrowUturnLeftOutline, Icon56InfoOutline } from '@vkontakte/icons';
import Swipe from './Swipe';
import Value from './Value';
import Input from './Input';
import Textarea from '../Elements/Textarea';

const Response = ({ s }) => {
    const { res, tabs, wrapContent = true, wrapJson = false, wrapRaw = false } = s.g();

    return (
        <>
            {typeof res === 'object' ?
                res.error ?
                    <>
                        <div className="Div">
                            <div className="title">Ошибка</div>
                            <Value>
                                <div dangerouslySetInnerHTML={{ __html: res.error }} style={{ overflowWrap: 'break-word', wordBreak: 'normal' }} />
                            </Value>
                        </div>
                        <div className="Div title">Редиректы</div>
                        {res.redirects.map((e, k) =>
                            <div className="Div" key={k} style={{ display: "flex", alignItems: "center", ...(k === 0 ? { marginTop: 0 } : {}) }}>
                                {k !== 0 && <Icon20ArrowUturnLeftOutline style={{ marginRight: "8px", transform: "rotate(180deg)" }} />}
                                <Textarea style={{ flex: 1 }} value={e} readOnly />
                            </div>
                        )}
                    </>
                    :
                    <>
                        <Swipe style={{ marginTop: "12px", marginBottom: "12px" }} s={s} id="tabs">
                            <Tabs mode="buttons" style={{ padding: 0 }}>
                                <TabsItem
                                    selected={tabs === 'res'}
                                    onClick={() => s.s('tabs', 'res')}
                                    style={{ border: '1px solid var(--panel_tab_active_background)' }}
                                >Ответ</TabsItem>
                                <TabsItem
                                    selected={tabs === 'content'}
                                    onClick={() => s.s('tabs', 'content')}
                                    style={{ border: '1px solid var(--panel_tab_active_background)' }}
                                >Контент</TabsItem>
                                {res.redirects.length > 1 && <TabsItem
                                    selected={tabs === 'redirects'}
                                    onClick={() => s.s('tabs', 'redirects')}
                                    style={{ border: '1px solid var(--panel_tab_active_background)' }}
                                >Редиректы</TabsItem>}
                                {res.html && <TabsItem
                                    selected={tabs === 'html'}
                                    onClick={() => s.s('tabs', 'html')}
                                    style={{ border: '1px solid var(--panel_tab_active_background)' }}
                                >HTML</TabsItem>}
                                {res.jsonHtml && <TabsItem
                                    selected={tabs === 'json'}
                                    onClick={() => s.s('tabs', 'json')}
                                    style={{ border: '1px solid var(--panel_tab_active_background)' }}
                                >JSON</TabsItem>}
                                <TabsItem
                                    selected={tabs === 'raw'}
                                    onClick={() => s.s('tabs', 'raw')}
                                    style={{ border: '1px solid var(--panel_tab_active_background)' }}
                                >RAW</TabsItem>
                                <div style={{ width: "12px", display: "inline" }}>&nbsp;</div>
                            </Tabs>
                        </Swipe>
                        {tabs === 'res' ?
                            <>
                                <div className="Div" style={{ display: "flex" }}>
                                    <div style={{ flex: 2, marginRight: "5px" }}>
                                        <div className="title">URL</div>
                                        <Input value={res.url} readOnly />
                                    </div>
                                    <div style={{ flex: 1 }}>
                                        <div className="title">Время</div>
                                        <Input value={(res.time) + 'мс'} readOnly />
                                    </div>
                                </div>
                                <div className="Div" style={{ display: "flex" }}>
                                    <div style={{ flex: 2, marginRight: "5px" }}>
                                        <div className="title">Код</div>
                                        <Input value={res.code} readOnly />
                                    </div>
                                    <div style={{ flex: 1 }}>
                                        <div className="title">Метод</div>
                                        <Input value={res.method} readOnly />
                                    </div>
                                </div>
                                <div className="Div" style={{ flex: 1 }}>
                                    <div className="title">IP-адрес</div>
                                    <Input value={res.ip} readOnly />
                                </div>
                                <div className="Div">
                                    <div className="title">Заголовки</div>
                                    <Value>
                                        <div dangerouslySetInnerHTML={{ __html: res.headersHtml }} />
                                    </Value>
                                </div>
                            </>
                            : tabs === 'content' ?
                                <>
                                    <SimpleCell
                                        disabled
                                        style={{ paddingLeft: "12px", paddingRight: "12px" }}
                                        after={<Switch style={{ cursor: "pointer" }} checked={wrapContent} onChange={() => s.s('wrapContent', !wrapContent)} />}
                                    >
                                        Переносы строк
                                    </SimpleCell>
                                    <Value
                                        className="Div"
                                        txtStyle={{ fontSize: '11pt', whiteSpace: wrapContent ? 'pre-wrap' : 'pre', overflow: 'auto' }}
                                        style={{ marginTop: 0 }}
                                    >{res.content}</Value>
                                </>
                                : tabs === 'html' ?
                                    <div className="Div">
                                        <iframe title="HTML" sandbox="allow-forms" srcDoc={res.html} style={{ height: document.body.offsetHeight - 125 }} />
                                    </div>
                                    : tabs === 'json' ?
                                        <>
                                            <SimpleCell
                                                disabled
                                                style={{ paddingLeft: "12px", paddingRight: "12px" }}
                                                after={<Switch style={{ cursor: "pointer" }} checked={wrapJson} onChange={() => s.s('wrapJson', !wrapJson)} />}
                                            >
                                                Переносы строк
                                                </SimpleCell>
                                            <Value
                                                className="Div"
                                                txtStyle={{ fontSize: '10.3pt', fontFamily: 'monospace', whiteSpace: wrapJson ? 'pre-wrap' : 'pre', overflow: 'auto' }}
                                                style={{ marginTop: 0 }}
                                            >
                                                <div dangerouslySetInnerHTML={{ __html: res.jsonHtml }} />
                                            </Value>
                                        </>
                                        : tabs === 'raw' ?
                                            <>
                                                <SimpleCell
                                                    disabled
                                                    style={{ paddingLeft: "12px", paddingRight: "12px" }}
                                                    after={<Switch style={{ cursor: "pointer" }} checked={wrapRaw} onChange={() => s.s('wrapRaw', !wrapRaw)} />}
                                                >
                                                    Переносы строк
                                                    </SimpleCell>
                                                <Value
                                                    className="Div"
                                                    txtStyle={{ fontSize: '10.3pt', fontFamily: 'monospace', whiteSpace: wrapRaw ? 'pre-wrap' : 'nowrap', overflow: 'auto' }}
                                                    style={{ marginTop: 0 }}
                                                >
                                                    <div dangerouslySetInnerHTML={{ __html: res.rawHtml }} />
                                                </Value>
                                            </>
                                            : tabs === 'redirects' ?
                                                <>
                                                    {res.redirects.map((e, k) =>
                                                        <div className="Div" key={k} style={{ display: "flex", alignItems: "center" }}>
                                                            {k !== 0 && <Icon20ArrowUturnLeftOutline style={{ marginRight: "8px", transform: "rotate(180deg)" }} />}
                                                            <Value style={{ flex: 1 }}>{e}</Value>
                                                        </div>
                                                    )}
                                                </>
                                                : null}
                    </>
                :
                <div className="placeholder" align="center">
                    <Icon56InfoOutline style={{ marginBottom: "5px" }} />
                    <div>Результат запроса появится<br />здесь после отправки</div>
                </div>}
        </>
    );
}

export default Response;