import { useEffect } from "react";
import { Group, Panel, PanelHeader, View, Button, Alert, PanelHeaderButton } from "@vkontakte/vkui";
import { Icon24Delete, Icon24Add, Icon56SettingsOutline, Icon28DeleteOutlineAndroid } from '@vkontakte/icons';
import Input from "../Elements/Input";
import Textarea from "../Elements/Textarea";

const Params = ({ id, s }) => {
    const apiurl = require('url');
    const { path, url } = s.g();

    useEffect(() => {
        const httpUrl = url.match(/^(https?:\/\/)/i) || url.startsWith('/') ? url : 'http://' + url;
        const pre = (apiurl.parse(httpUrl).query || '').split('&');
        let res = [];
        for (const i of pre) {
            const a = i.substr(0, i.indexOf('='));
            const b = i.substr(i.indexOf('=') + 1);
            if ((a + b).trim() !== '') res.push([decodeURIComponent(a), decodeURIComponent(b)]);
        }
        s.s('path', res);
        // eslint-disable-next-line
    }, []);

    return (
        <View id={id} activePanel="params">
            <Panel id="params">
                <PanelHeader
                    left={<PanelHeaderButton disabled={!path || path.length === 0} onClick={() => s.s('popout',
                        <Alert
                            header="Удалить ВСЕ параметры?"
                            text="Подтвердите действие."
                            onClose={() => s.s('popout', null)}
                            actions={[
                                {
                                    title: "Отмена",
                                    autoclose: true,
                                    mode: 'cancel'
                                },
                                {
                                    title: "Удалить",
                                    mode: "destructive",
                                    action: () => {
                                        s.s('path', []);
                                        s.s('popout', null);
                                    }
                                }
                            ]}
                        />
                    )}><Icon28DeleteOutlineAndroid /></PanelHeaderButton>}
                >URL-параметры</PanelHeader>

                <Group>
                    {path ?
                        <div>
                            <div className="Div">
                                <Button
                                    before={<Icon24Add />}
                                    size="m"
                                    stretched
                                    onClick={() => s.s('path', e => {
                                        let path = [...e];
                                        path.push(['', '']);
                                        return path;
                                    })}
                                >Добавить</Button>
                            </div>
                            {path.length !== 0 ?
                                <div>
                                    {path.map((i, k) => (
                                        <div className="Div" key={k}>
                                            {k !== 0 && <div className="sep" style={{ margin: "12px 0" }} />}
                                            <div style={{ display: "flex", marginBottom: "5px", alignItems: "stretch" }}>
                                                <Input style={{ flex: 1 }} placeholder="Параметр" value={i[0]} onChange={({ target: { value: e } }) => s.s('path', i => {
                                                    let path = [...i];
                                                    path[k].splice(2, 1);
                                                    path[k][0] = e;
                                                    return path;
                                                })} />
                                                <Button size="m" mode="destructive" onClick={() => s.s('popout',
                                                    <Alert
                                                        header="Удалить параметр?"
                                                        text="Подтвердите действие."
                                                        onClose={() => s.s('popout', null)}
                                                        actions={
                                                            [
                                                                {
                                                                    title: "Отмена",
                                                                    autoclose: true,
                                                                    mode: 'cancel'
                                                                },
                                                                {
                                                                    title: "Удалить",
                                                                    mode: "destructive",
                                                                    action: () => {
                                                                        s.s('path', i => {
                                                                            let params = [...i];
                                                                            params.splice(k, 1);
                                                                            return params;
                                                                        });
                                                                        s.s('popout', null);
                                                                    }
                                                                }
                                                            ]
                                                        }
                                                    />)} style={{ marginLeft: "5px" }}><Icon24Delete /></Button>
                                            </div>
                                            <Textarea placeholder="Значение" txtStyle={{ maxHeight: '87px' }} value={i[1]} onChange={({ target: { value: e } }) => s.s('path', i => {
                                                let path = [...i];
                                                path[k].splice(2, 1);
                                                path[k][1] = e;
                                                return path;
                                            })} />
                                        </div>
                                    ))}
                                </div>
                                :
                                <div className="placeholder" align="center">
                                    <Icon56SettingsOutline />
                                    <div>Список URL-параметров пуст</div>
                                    <code>/?v=1.2&#38;message=hello%20world</code>
                                </div>
                            }
                        </div>
                        :
                        <div className="placeholder" align="center">
                            Загрузка...
                        </div>
                    }
                </Group>
            </Panel>
        </View>
    );
}

export default Params;