import { useState } from 'react';
import { usePlatform } from '@vkontakte/vkui';

const Input = ({ className, txtStyle = {}, style = {}, onBlur = () => {}, title, ...props }) => {
    const platform = usePlatform();
    const [border, setBorder] = useState(false);

    return (
        <div className={className} style={{ position: "relative", width: "100%", ...style }}>
            <input className={title ? 'withTitle' : ''} style={txtStyle} onFocus={() => setBorder(true)} onBlur={(e) => { setBorder(false); onBlur(e); }} {...props} />
            <div className={`border border--${platform}`} style={border ? { border: '1px solid var(--accent)' } : {}} />
            <div className={`txt-bg border--${platform}`} />
            {title && <div className='inputTitle'>{title}</div>}
        </div>
    );
}

export default Input;