import { Button, Alert } from "@vkontakte/vkui";
import { Icon24AddSquareOutline, Icon56ArticleOutline, Icon24Cancel, Icon20DeleteOutline } from '@vkontakte/icons';
import Textarea from '../Elements/Textarea';
import Input from '../Elements/Input';

const Headers = ({ s }) => {
    const { headers } = s.g();

    return (
        <>
            <div className="Div" style={{ display: "flex" }}>
                <Button
                    before={<Icon24AddSquareOutline width={20} height={20} />}
                    size="m"
                    stretched
                    style={{ marginRight: "8px" }}
                    onClick={() => s.s('headers', e => {
                        let headers = [...e];
                        headers.push(['', '']);
                        return headers;
                    })}
                >Добавить</Button>
                <Button
                    size="m"
                    stretched
                    mode="destructive"
                    disabled={headers.length === 0}
                    before={<Icon20DeleteOutline />}
                    onClick={() => s.s('popout',
                        <Alert
                            header="Удалить ВСЕ заголовки?"
                            text="Подтвердите действие."
                            onClose={() => s.s('popout', null)}
                            actions={[
                                {
                                    title: "Отмена",
                                    autoclose: true,
                                    mode: 'cancel'
                                },
                                {
                                    title: "Удалить",
                                    mode: "destructive",
                                    action: () => {
                                        s.s('headers', []);
                                        s.s('popout', null);
                                    }
                                }
                            ]}
                        />
                    )}
                >Очистить всё</Button>
            </div>
            {headers.length === 0 ?
                <div className="placeholder" align="center">
                    <Icon56ArticleOutline />
                    <div>Список заголовков пуст</div>
                </div>
                :
                <div>
                    {headers.map((i, k) => (
                        <div className="Div" style={k === headers.length - 1 ? { marginBottom: 0 } : {}} key={k}>
                            <div style={{ display: "flex", alignItems: "stretch" }}>
                                <Input className={i[2] ? "errorInput" : ""} txtStyle={{ padding: '7px 9px', fontSize: '11pt' }} style={{ flex: 1 }} placeholder="Заголовок" value={i[0]} onChange={(e) => s.s('headers', i => {
                                    let headers = [...i];
                                    headers[k].splice(2, 1);
                                    headers[k][0] = e.target.value.toLowerCase().replace(/ /g, '');
                                    return headers;
                                })} />
                                <Input txtStyle={{ padding: '7px 9px', fontSize: '11pt' }} style={{ flex: 1, marginLeft: "5px" }} placeholder="Значение" value={i[1]} onChange={(e) => s.s('headers', i => {
                                    let headers = [...i];
                                    headers[k][1] = e.target.value.replace(/\n/g, '');
                                    return headers;
                                })} />
                                <Button onClick={() => s.s('popout',
                                    <Alert
                                        header="Удалить заголовок?"
                                        text="Подтвердите действие."
                                        onClose={() => s.s('popout', null)}
                                        actions={
                                            [
                                                {
                                                    title: "Отмена",
                                                    autoclose: true,
                                                    mode: 'cancel'
                                                },
                                                {
                                                    title: "Удалить",
                                                    mode: "destructive",
                                                    action: () => {
                                                        s.s('headers', i => {
                                                            let headers = [...i];
                                                            headers.splice(k, 1);
                                                            return headers;
                                                        });
                                                        s.s('popout', null);
                                                    }
                                                }
                                            ]
                                        }
                                    />)} size="m" mode="secondary" style={{ marginLeft: "5px", padding: '0 8px' }}><Icon24Cancel width={16} height={16} /></Button>
                            </div>
                        </div>
                    ))}
                </div>
            }
        </>
    );
}

export default Headers;