import { Alert, Button, Group, Panel, PanelHeader, View, PanelHeaderButton } from '@vkontakte/vkui';
import { Icon56ArticleOutline, Icon24Add, Icon24Delete, Icon28DeleteOutlineAndroid } from '@vkontakte/icons';
import Input from '../Elements/Input';
import Textarea from '../Elements/Textarea';

const Headers = ({ id, s }) => {
    const { headers } = s.g();

    return (
        <View id={id} activePanel="headers">
            <Panel id="headers">

                <PanelHeader
                    left={<PanelHeaderButton disabled={headers.length === 0} onClick={() => s.s('popout',
                        <Alert
                            header="Удалить ВСЕ заголовки?"
                            text="Подтвердите действие."
                            onClose={() => s.s('popout', null)}
                            actions={[
                                {
                                    title: "Отмена",
                                    autoclose: true,
                                    mode: 'cancel'
                                },
                                {
                                    title: "Удалить",
                                    mode: "destructive",
                                    action: () => {
                                        s.s('headers', []);
                                        s.s('popout', null);
                                    }
                                }
                            ]}
                        />
                    )}><Icon28DeleteOutlineAndroid /></PanelHeaderButton>}
                >Заголовки</PanelHeader>

                <Group>
                    <div className="Div">
                        <Button
                            before={<Icon24Add />}
                            size="m"
                            stretched
                            onClick={() => s.s('headers', e => {
                                let headers = [...e];
                                headers.push(['', '']);
                                return headers;
                            })}
                        >Добавить</Button>
                    </div>
                    {headers.length === 0 ?
                        <div className="placeholder" align="center">
                            <Icon56ArticleOutline />
                            <div>Список заголовков пуст</div>
                        </div>
                        :
                        <div>
                            {headers.map((i, k) => (
                                <div className="Div" key={k}>
                                    {k !== 0 && <div className="sep" style={{ margin: "12px 0" }} />}
                                    <div style={{ display: "flex", marginBottom: "5px", alignItems: "stretch" }}>
                                        <Input className={i[2] ? "errorInput" : ""} style={{ flex: 1 }} placeholder="Заголовок" value={i[0]} onChange={(e) => s.s('headers', i => {
                                            let headers = [...i];
                                            headers[k].splice(2, 1);
                                            headers[k][0] = e.target.value.toLowerCase().replace(/ /g, '');
                                            return headers;
                                        })} />
                                        <Button onClick={() => s.s('popout',
                                            <Alert
                                                header="Удалить заголовок?"
                                                text="Подтвердите действие."
                                                onClose={() => s.s('popout', null)}
                                                actions={
                                                    [
                                                        {
                                                            title: "Отмена",
                                                            autoclose: true,
                                                            mode: 'cancel'
                                                        },
                                                        {
                                                            title: "Удалить",
                                                            mode: "destructive",
                                                            action: () => {
                                                                s.s('headers', i => {
                                                                    let headers = [...i];
                                                                    headers.splice(k, 1);
                                                                    return headers;
                                                                });
                                                                s.s('popout', null);
                                                            }
                                                        }
                                                    ]
                                                }
                                            />)} size="m" mode="destructive" style={{ marginLeft: "5px" }}><Icon24Delete /></Button>
                                    </div>
                                    <Textarea placeholder="Значение" value={i[1]} txtStyle={{ maxHeight: '87px' }} onChange={(e) => s.s('headers', i => {
                                        let headers = [...i];
                                        headers[k][1] = e.target.value.replace(/\n/g, '');
                                        return headers;
                                    })} />
                                </div>
                            ))}
                        </div>
                    }
                </Group>
            </Panel>
        </View>
    );
}

export default Headers;